// initial state


import axios from "axios";

const state = {
  allUsersList: [],
};


/**
 * ----- GETTERS -----
 **/

const getters = {

  getAllUsers: (state) => {
    console.log('-----------GET----------', state.allUsersList);
    return state.allUsersList;

  },

};


/**
 * ----- ACTIONS -----
 **/

const actions = {

  allUsersList({commit}) {
    let  headers = {
      'Content-Type': 'application/x-www-form-urlencode',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Headers': '*'


    };
    axios
      .post('https://app.causeffect.nl/public/api/list_of_the_user.php', headers)
      .then(function (response) {
          commit('allUsersListMut', response.data);

      });
  }
};


/**
 * ----- MUTATIONS -----
 * */

const mutations = {
  allUsersListMut (state, value) {
    state.allUsersList = state.allUsersList.concat(value)
  },

};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};



